import Vue from 'vue'
import Router from 'vue-router'
import Liveview from '@/components/liveview.vue'
import { IonicVueRouter } from '@ionic/vue'
Vue.use(IonicVueRouter)

export default  new IonicVueRouter({
  routes: [
    {
      path: '/',
      name: 'Liveview',
      component: Liveview
    }
  ]
})



